.container {
  position: fixed;
  height: 100%;
  width: 100%;
  overflow: auto;
}
table {
  background-color: #ffffff;
}
table td {
  border-bottom: 1px solid #eee;
  border-right: 1px solid #eee;
  text-align: center;
  line-height: 30px;
  height: 30px;
}
table td.rows {
  background-color: #f1f1f1;
  border-top: none;
  border-left: none;
  border-bottom: 1px solid #aaa;
  border-right: 1px solid #aaa;
}
table td.cols {
  background-color: #f1f1f1;
  width: 40px;
  border-top: none;
  border-left: none;
  border-bottom: 1px solid #aaa;
  border-right: 1px solid #aaa;
}
.cross {
  position: absolute;
  height: 30px;
  width: 40px;
  left:0;
  top:0;
  z-index: 5;
  border-bottom: 1px solid #aaa;
  border-right: 1px solid #aaa;
  background-color: #f1f1f1;
}
.wrapper {
  height: 100%;
  width: 100%;
  position: relative;
}
